* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@media screen and (max-width: 600px) {
  .alpha-wrapper {
    width: 100%;
  }
  header {
    height: 90vh;
    padding: 0.2rem;
  }
  header nav {
    flex-direction: column;
    padding: 1rem;
  }
  header .wrapper {
    background: white;
    opacity: 0.5;
    display: flex;
    flex-direction: column;
    width: 5rem;
    height: 80vh;
    width: 100%;
    border-radius: 10px;
  }
  header .wrapper ul {
    display: flex;
    flex-direction: column;
    color: black;
  }
  header .wrapper .last-div {
    display: flex;
    flex-direction: column;
  }
  header .wrapper .sign-up {
    width: 8rem;
    background: linear-gradient(45deg, #ff8e70, #ff4359);
    color: white;
  }
  header .wrapper .login {
    width: 8rem;
    height: 2rem;
    color: black;
  }
}